import React from 'react';
import { slide as Menu } from 'react-burger-menu';
import ScrollTo from '../components/ScrollTo';

export default class Nav extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      menuOpen: false
    };
  }

  handleStateChange(state) {
    this.setState({ menuOpen: state.isOpen });
  }

  toggleMenu() {
    this.setState({ menuOpen: !this.state.menuOpen });
  }

  closeMenu() {
    this.setState({ menuOpen: false });
  }

  renderMobileMenu() {
    return (
      <Menu
        right
        isOpen={this.state.menuOpen}
        onStateChange={state => this.handleStateChange(state)}
      >
        <ScrollTo onClick={() => this.closeMenu()} href="about">
          About
        </ScrollTo>
        <ScrollTo onClick={() => this.closeMenu()} href="team">
          Team
        </ScrollTo>
        <ScrollTo onClick={() => this.closeMenu()} href="join-us">
          Join us
        </ScrollTo>
        <style jsx global>{`
          .menu-item {
            display: block;
            color: var(--color-tertiary);
            text-transform: uppercase;
            font-size: 0.8rem;
            text-decoration: none;
            font-family: var(--font-primary);
            letter-spacing: 1px;
            margin-bottom: 2rem;
          }
          /* Position and sizing of burger button */
          .bm-burger-button {
            position: relative;
            width: 1.4rem;
            height: 14px;
          }

          /* Color/shape of burger icon bars */
          .bm-burger-bars {
            position: absolute;
            height: 2px !important;
            border-radius: 4px;
            left: 0;
            right: 0;
            background-color: var(--color-primary);
          }

          .bm-burger-bars:nth-child(1) {
            top: 0 !important;
          }
          .bm-burger-bars:nth-child(2) {
            top: 50% !important;
            transform: translateY(-50%);
          }
          .bm-burger-bars:nth-child(3) {
            top: 100% !important;
            transform: translateY(-100%);
          }

          .bm-cross-button {
            right: 4vw !important;
            top: 4vw !important;
          }

          /* Color/shape of close button cross */
          .bm-cross {
            cursor: pointer;
            background: var(--color-font);
            height: 20px !important;
          }

          /* General sidebar styles */
          .bm-menu {
            background: var(--color-quinary) !important;
            padding: 4em 1.5em 0;
            font-size: 1.15em;
            transition: all 400ms ease;
          }

          .bm-menu button {
            width: 80%;
            margin: 1rem 0;
          }

          .bm-menu button:first-of-type {
            margin-top: 30%;
          }

          .bm-menu-wrap {
            top: 0;
          }

          /* Wrapper for item list */
          .bm-item-list {
            padding: 0.8em;
          }

          /* Styling of overlay */
          .bm-overlay {
            top: 0;
            left: 0;
            width: 100vw !important;
            height: 100vh !important;
            z-index: 1001 !important;
            background: var(--color-quinary) !important;
          }
        `}</style>
      </Menu>
    );
  }

  render() {
    return <nav>{this.renderMobileMenu()}</nav>;
  }
}
